<template>
  <div class="fansnumber">
    <div class="person">
      <div class="lis">
        <img class="page" src="@/images/page.png" alt="">
        <span>发布{{userInfo.question}}篇文章</span>
      </div>
      <div class="lis">
        <img class="write" src="@/images/write.png" alt="">
        <span>参与{{userInfo.attention}}次回答</span>
      </div>
    </div>
    <div class="atten">
      <div class="att">
        <div class="r-top">关注</div>
        <div class="num">{{fansNum}}</div>
      </div>
      <div class="follower">
        <div class="r-top">粉丝</div>
        <div class="num">{{attentionNum}}</div>
      </div>
    </div>

  </div>
</template>

<script>
  export default {
    name: "fansnumber",
    data() {
      return {
        fansNum: 0,
        attentionNum: 0,
        userInfo: {},
        uid: '',
      }
    },
    props: [
      "otheruid"
    ],
    mounted() {
      let otheruid=this.$route.query.otheruid;
      if(otheruid){
        this.uid=otheruid;
      }else {
        this.uid=this.$store.state.userInfo.uid;
      }
    setTimeout(()=>{
      this.getuser();
      this.getnumber();
    },1000)
    },
    watch: {
      $route(){
        let otheruid=this.$route.query.otheruid;
        if(otheruid){
           this.uid=otheruid;
            }else {
          this.uid=this.$store.state.userInfo.uid;
        }
        this.getuser();
        this.getnumber();
      }
    },
    methods: {
//获取用户信息
      getuser() {
        this.$post('index/data/member_info', {uid: this.uid}).then(res => {
          this.userInfo = res.data;
        }).catch(err => {
          console.log(err);
        })
      },
      //获取数量
      getnumber() {
        this.$post("index/data/question_user_count", {uid: this.uid}).then(res => {
          this.fansNum = res.data.fans;
          this.attentionNum = res.data.attention;
          /*  if (res.data.result == 400) {
              this.$message({type: 'warning', message: res.data.msg})
            }*/
        }).catch(err => {
          console.log(err);
        })
      }
    }
  }
</script>

<style scoped lang="scss">
  .person {
    margin-top: 14px;
    background: rgba(255, 255, 255, 1);
    box-shadow: 0px 3px 12px 0px rgba(0, 0, 0, 0.22);
    border-radius: 6px;
    height: 168px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    box-sizing: border-box;

    .lis {
      font-size: 16px;
      color: rgba(51, 51, 51, 1);
      line-height: 25px;
      box-sizing: border-box;
      display: flex;
      align-items: center;
      &:last-child{
        margin-top: 20px;
      }
      .page{
        width: 15px;
        height: 17px;
      }
      .write{
        width: 18px;
        height: 16px;
      }
      span {
        font-weight: 700;
        margin-left: 16px;
        font-size: 16px;
        color: rgba(51, 51, 51, 1);
        line-height: 30px;
      }
    }
  }

  .atten {
    margin-top: 13px;
    margin-bottom: 10px;
    display: flex;
    justify-content: center;
    text-align: center;
    align-items: center;
    height: 74px;
    background: rgba(255, 255, 255, 1);
    box-shadow: 0px 3px 12px 0px rgba(0, 0, 0, 0.22);
    border-radius: 4px;
    padding: 0;

    > div {
      width: 50%;
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }

    .att {
      border-right: 1px solid rgba(221, 221, 221, 1);
    }

    .follower {
    }

    .r-top {
      font-size: 14px;
      font-weight: 400;
      color: rgba(153, 153, 153, 1);
      line-height: 25px;
    }

    .num {
      font-size: 18px;
      font-weight: bold;
      color: rgba(51, 51, 51, 1);
      line-height: 25px;
    }
  }

</style>



