<template>
  <div class="editUser" v-wechat-title="$route.meta.title">
    <div class="operation">
      <div class="left">
        <div class="top">
          <div class="title">个人信息</div>
          <div class="back" @click="$router.push('user')">返回个人中心 <i class="iconfont icon-jiantou"></i></div>
        </div>
        <div class="edit-box">
          <div class="logo">
            <div class="label">用户头像</div>
            <div class="head">
              <img id="portrait" :src="userInfo.head" alt="">
              <div class="cover">
                <input type="file" id="saveImage" name="myphoto" accept="image/png,image/gif,image/jpeg"
                       ref="new_company_image">
              </div>
            </div>
          </div>
          <div class="username">
            <div class="label">用户名</div>
            <div class="edit-name">
              <div contenteditable="true" class="name">
                <el-input v-if="editname" v-model="name" style="width: 150px" size="small"
                          placeholder="请输入姓名"></el-input>
                <!--<input v-if="editname"  type="text" v-model="userInfo.username">-->
                <span v-else @click="editname=true">{{userInfo.username}}</span>
              </div>
              <div class="edit-btn" @click="savename">
                <img src="@/images/write.png" alt="">
                修改
              </div>
            </div>
          </div>
          <div class="intro">
            <div class="label">个人介绍</div>
            <div class="word" v-if="!introshow" @click="introshow=true">{{bio}}</div>
            <div class="editer-intro" v-if="introshow">
              <el-input
                type="textarea"
                :autosize="{ minRows: 2, maxRows: 4}"
                placeholder="添加一句话介绍自己"
                v-model="bio">
              </el-input>
              <div class="btns">
                <el-button size="small" type="primary" @click="savabio">保存</el-button>
                <el-button size="small" @click="introshow=false">取消</el-button>
              </div>
            </div>
          </div>
        </div>

      </div>
      <div class="right">
        <div class="userinfo">
          <div class="header">
            <img :src="userInfo.head" alt="">
          </div>
          <div class="name">{{userInfo.username}}</div>
          <div class="dec">{{userInfo.bio}}</div>
        </div>

        <!--<fansnumber></fansnumber>-->

      </div>
    </div>
  </div>
</template>

<script>
  import fansnumber from "./fansnumber";
  import {mapState} from 'vuex'

  export default {
    name: "editUser",
    data() {
      return {
        editname: false,
        name: '',
        introshow: false,
        bio: ''
      }
    }, components: {
      fansnumber
    },
    mounted() {
      this.init();
    },
    computed: mapState([
      'userInfo', 'isLogin'
    ]),
    methods: {
      //用户信息
      init() {
      setTimeout(()=>{
        this.name = this.userInfo.username;
        this.bio = this.userInfo.bio;
      },200)
        // this.$post("http://localhost:8080/news/user").then(res => {
        //   this.userInfo = res.data;
        // }).catch(err => {
        //   console.log(err);
        // })
      },
      great: function () {
        document.getElementById('saveImage').onchange = function () {
          var imgFile = this.files[0];
          var fr = new FileReader();
          fr.onload = function () {
            document.getElementById('portrait').src = fr.result;
          };
          fr.readAsDataURL(imgFile);
        }
      },
      //修改名称
      savename() {
        let username = this.name;
        let uid = this.userInfo.uid;
        let bio = this.userInfo.bio;
        this.$post('index/data/member_edit', {username, uid, bio}).then(res => {
          if (res.data.result == 200) {
            this.editname = false;
            this.$message.success(res.data.msg);
            this.userInfo.username = this.name;
            let userinfo = this.userInfo;
            this.$store.commit("setLogin", userinfo); //登录状态保存在$store
          } else {
            this.$message.warning(res.data.msg)
          }
        }).catch(err => {
          console.log(err);
        })
      },
      //修改简介
      savabio() {
        this.introshow = false;
        let username = this.userInfo.name;
        let uid = this.userInfo.uid;
        let bio = this.bio;
        this.$post('index/data/member_edit', {username, uid, bio}).then(res => {
          if (res.data.result == 200) {
            this.$message.success(res.data.msg);
            this.userInfo.bio = this.bio;
            let userinfo = this.userInfo;
            this.$store.commit("setLogin", userinfo); //登录状态保存在$store
          } else {
            this.$message.warning(res.data.msg)
          }
        }).catch(err => {
          console.log(err);
        })
      }
    }
  }
</script>

<style scoped lang="scss">
  $main: #409eff !default;
  .operation {
    width: 1180px;
    margin: 36px auto 0;
    display: flex;
    justify-content: space-between;

    > .left {
      width: 790px;
      box-shadow: 0px 3px 7px 0px rgba(0, 0, 0, 0.22);
      box-sizing: border-box;
      padding: 49px 72px;

      .top {
        display: flex;
        justify-content: space-between;

        .title {
          font-size: 28px;
          font-weight: bold;
          color: rgba(51, 51, 51, 1);
          line-height: 44px;
        }

        .back {
          font-size: 14px;
          font-weight: 400;
          color: rgba(153, 153, 153, 1);
          line-height: 44px;
          cursor: pointer;

          i {
            font-size: 14px;
          }
        }
      }

      .edit-box {
        margin-top: 59px;
        padding-left: 56px;
        box-sizing: border-box;

        .label {
          width: 130px;
          font-size: 16px;
          font-weight: bold;
          color: rgba(51, 51, 51, 1);
          line-height: 20px;
        }

        > div {
          display: flex;
        }

        .logo {

          .label {
            line-height: 70px;
          }

          .head {
            width: 70px;
            height: 70px;
            border-radius: 50%;
            position: relative;
            overflow: hidden;


            > img {
              position: relative;
              width: 100%;
              height: 100%;
            }

            &:hover .cover {
              display: inline-block;
            }

            #saveImage {
              opacity: 0;
              width: 100%;
              height: 100%;
              position: absolute;
              left: 0;
              top: 0;
              z-index: -100
            }

            .cover {
              z-index: 100;
              display: none;
              cursor: pointer;
              content: '';
              background-color: #000;
              position: absolute;
              left: 0;
              top: 0;
              width: 100%;
              height: 100%;
              border-radius: 50%;
              background: url('~@/images/add.png') center center no-repeat;
            }
          }
        }

        .username {
          margin-top: 76px;

          .edit-name {
            display: flex;

            .name {
              font-size: 16px;
              font-weight: 400;
              color: rgba(51, 51, 51, 1);
              line-height: 20px;
              margin-right: 41px;
            }

            .edit-btn {
              cursor: pointer;
              font-size: 16px;
              font-weight: 400;
              color: rgba(0, 150, 255, 1);
              line-height: 20px;
              display: flex;
              align-items: center;

              > img {
                width: 16px;
                height: 16px;
                margin-right: 5px;
                vertical-align: middle;
              }
            }
          }


        }

        .intro {
          margin-top: 52px;

          .word {
            cursor: pointer;
            font-size: 16px;
            font-weight: 400;
            color: rgba(0, 150, 255, 1);
            line-height: 20px;
          }

          .editer-intro {
            width: 470px;

            .btns {
              margin-top: 14px;
              display: flex;
            }
          }
        }
      }
    }

    > .right {
      width: 350px;

      .userinfo {
        text-align: center;
        width: 100%;
        height: 254px;
        background: rgba(255, 255, 255, 1);
        box-shadow: 0px 3px 12px 0px rgba(0, 0, 0, 0.12);
        border-radius: 4px;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        align-items: center;
        box-sizing: border-box;

        .header {
          width: 82px;
          height: 82px;
          border-radius: 50%;
          overflow: hidden;

          img {
            width: 100%;
            height: 100%;
          }
        }

        .name {
          width: 108px;
          font-size: 26px;
          font-weight: bold;
          color: rgba(51, 51, 51, 1);
          line-height: 26px;
        }

        .dec {
          width: 203px;
          height: 25px;
          font-size: 16px;
          font-weight: 400;
          color: rgba(102, 102, 102, 1);
          line-height: 16px;
          display: block;
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
        }
      }


    }
  }
</style>



